import React from 'react';
import { List, InputItem } from 'antd-mobile';
import { connectAlita } from 'redux-alita';
import { createForm } from 'rc-form';
import Button from '@material-ui/core/Button';
import './login.less'


// 通过自定义 moneyKeyboardWrapProps 修复虚拟键盘滚动穿透问题
// https://github.com/ant-design/ant-design-mobile/issues/307
// https://github.com/ant-design/ant-design-mobile/issues/163
const isIPhone = new RegExp('\\biPhone\\b|\\biPod\\b', 'i').test(window.navigator.userAgent);
let moneyKeyboardWrapProps;
if (isIPhone) {
	moneyKeyboardWrapProps = {
		onTouchStart: e => e.preventDefault(),
	};
}


class Login extends React.Component {
	state = {
		userName: '',
		password: '',
	}
	componentDidMount() {
		const { setAlitaState } = this.props;
		setAlitaState({ stateName: 'auth', data: null });
	}
	componentDidUpdate(prevProps) { // React 16.3+弃用componentWillReceiveProps
		const { auth: nextAuth = {}, history } = this.props;
		console.log(nextAuth)
		// const { history } = this.props;
		if (nextAuth.data && nextAuth.data.uid) { // 判断是否登陆
			localStorage.setItem('user', JSON.stringify(nextAuth.data));
			history.push('/');
		}
	}
	handleSubmit = () => {
		// var x= require('./got.mp4')
		// console.log(x)
		const { getFieldsValue } = this.props.form;
		var data = getFieldsValue()
		console.log(getFieldsValue())
		const { setAlitaState } = this.props;
		if (data.userName === 'admin' && data.password === 'admin'){
			// setAlitaState({ funcName: 'admin', stateName: 'auth' });
			var data = {

			}
			setAlitaState({ funcName: 'admin', stateName: 'auth' });
		}
		if (data.userName === 'guest' && this.state.password === 'guest') setAlitaState({ funcName: 'guest', stateName: 'auth' });
	};
	gitHub = () => {
		window.location.href = 'https://github.com/login/oauth/authorize?client_id=792cdcd244e98dcd2dee&redirect_uri=http://localhost:3006/&scope=user&state=reactAdmin';
	};
	render() {
		const { getFieldProps } = this.props.form;
		return (
			<div className="login">
				<img className='logo' src="https://nova.invespec.com.cn/img/Nova-group-orange%20and%20gray.png" alt=""/>
				<div className='disc'>NOVAq是中国最具影响力的数字化督导平台</div>
				<div className="login-form" >
					<InputItem
						{...getFieldProps('userName')}
						clear
						placeholder="请输入账号..."
						ref={el => this.autoFocusInst = el}
					>账号</InputItem>
					<InputItem
						{...getFieldProps('password')}
						type="password"
						placeholder="请输入密码..."
					>密码</InputItem>
					<Button variant="contained" color="primary" className='start-btn' onClick={()=>this.handleSubmit()}>
						登录
					</Button>
				</div>
			</div>
		);
	}
}

export default connectAlita(['auth'])(createForm()(Login));
