const NUM_ROWS = 20;
let pageIndex = 0;

function genData(pIndex = 0) {
	const dataBlob = {};
	for (let i = 0; i < NUM_ROWS; i++) {
		const ii = (pIndex * NUM_ROWS) + i;
		dataBlob[`${ii}`] = `row - ${ii}`;
	}
	return dataBlob;
}

